import { computed, makeObservable, observable } from "mobx";
import want from "../image/want.jpeg";
import not_want from "../image/not_want.jpeg";
import apple from "../image/apple.jpg";
import give from "../image/give.jpeg";
import help from "../image/help.jpeg";
import no from "../image/no.jpeg";
import stop from "../image/stop.jpeg";
import wait from "../image/wait.jpeg";
import yes from "../image/yes.jpeg";

export default class Cards {

	_listCard = [];

	constructor() {
		makeObservable(this, {
			_listCard: observable,
			listCard: computed
		});

		let c = { header: "", title: " - ", src: want, group: "action" };
		this._listCard.push(c);

		c = { header: " ", title: " -  ", src: not_want, group: "action" };
		this._listCard.push(c);

		c = { header: "", title: " - ", src: give, group: "action" };
		this._listCard.push(c);

		c = { header: "", title: " - ", src: help, group: "action" };
		this._listCard.push(c);

		c = { header: "", title: " - ", src: yes, group: "action" };
		this._listCard.push(c);

		c = { header: "", title: " - ", src: no, group: "action" };
		this._listCard.push(c);

		c = { header: "", title: " - ", src: stop, group: "action" };
		this._listCard.push(c);

		c = { header: "", title: " - ", src: wait, group: "action" };
		this._listCard.push(c);

		c = { header: "", title: " - ", src: apple, group: "item" };
		this._listCard.push(c);
	}

	get listCard() {
		return this._listCard;
	}
}